.who-needs {
  box-sizing: border-box;
  min-height: 986px;
  padding: 50px 70px;

  background-image: url("../img/bg-who-needs.svg");
  background-repeat: no-repeat;
  background-position: -107px 24px;
}

.who-needs__heading {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  margin-bottom: 40px;

  @include bold(24px, 124.41%, $black);
}

.who-needs__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.who-needs__item-heading {
  box-sizing: border-box;

  text-transform: uppercase;

  @include semibold(14px, 143.55%, $blue);
}

.who-needs__item-description {
  box-sizing: border-box;

  @include regular(14px, 143.55%, $lightgrey);
}
