@font-face {
  font-style: normal;
  font-weight: normal;
  font-family: "montserrat-regular";

  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: normal;
  font-family: "montserrat-semibold";

  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: normal;
  font-family: "montserrat-bold";

  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}

.body {
  margin: 0;
}

.button {
  display: block;
  box-sizing: border-box;
  padding: 14px 35px;

  font-size: 14px;
  line-height: 1.5em;
  font-family: "montserrat-bold", "Arial", sans-serif;
  vertical-align: center;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;

  background: none;
  border: none;
  border-radius: 50px;
  cursor: pointer;

  transition: 0.1s ease-in-out;
}

.button--fill {
  background-color: #52d1bc;
}

.container {
  box-sizing: border-box;
  width: 320px;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
}

.header__container {
  position: relative;

  box-sizing: border-box;
  padding: 30px 8px;

  background-image: url("../img/bg-mobile-header-photo@1x.png"), url("../img/bg-mobile-header.svg");
  background-repeat: no-repeat;
  background-position: -480px -480px, -480px -480px;
}

@media (min-resolution: 1.5dppx) {
  .header__container {
    background-image: url("../img/bg-mobile-header-photo@2x.png"), url("../img/bg-mobile-header.svg");
  }
}

.nav {
  display: flex;
  box-sizing: border-box;
  margin-bottom: 72px;
}

.nav--no-js {
  margin-bottom: 160px;
}

.nav__cities {
  display: none;
}

.nav__list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  list-style: none;
}

.nav__item {
  box-sizing: border-box;
}

.nav__link {
  box-sizing: border-box;

  text-decoration: none;
}

.nav__join-button {
  display: block;
  box-sizing: border-box;
  padding: 0;

  font-size: 14px;
  line-height: 1.436em;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #ffffff;
  text-transform: none;

  background: none;
  border: none;
}

@media (max-width: 1440px) {
  @keyframes bubble {
    from {
      transform: scale(1);
    }

    to {
      transform: scale(12);
    }
  }

  @keyframes opacify {
    from {
      color: rgba(47, 128, 237, 0);
    }

    to {
      color: #2f80ed;
    }
  }

  .nav__list {
    position: absolute;
    top: 10px;
    right: 2px;
    z-index: 0;

    display: none;
    padding: 20px 35px 20px 10px;
    overflow: hidden;

    color: #2f80ed;

    border-radius: 10px;
  }

  .nav__list--open {
    animation: opacify;
    animation-duration: 0.15s;
    animation-delay: 0.15s;

    animation-fill-mode: both;
  }

  .nav__list--close {
    animation: opacify;
    animation-duration: 0.15s;
    animation-direction: reverse;

    animation-fill-mode: both;
  }

  .nav__list--visible {
    display: block;
  }

  .nav__list--no-js {
    display: block;

    color: #ffffff;
  }

  .nav__list::before {
    content: "";
    position: absolute;
    top: 6px;
    right: 0;
    z-index: -1;

    display: none;
    box-sizing: border-box;
    width: 42px;
    height: 42px;

    background-color: #ffffff;
    border-radius: 50%;
    transform: scale(12);
  }

  .nav__list--visible::before {
    display: block;
  }

  .nav__list--open::before {
    animation-name: bubble;
    animation-duration: 0.3s;

    animation-fill-mode: both;
  }

  .nav__list--close::before {
    animation-name: bubble;
    animation-duration: 0.3s;
    animation-direction: reverse;

    animation-fill-mode: both;
  }

  .nav__link {
    font-size: 14px;
    line-height: 143.6%;
    font-family: "montserrat-regular", "Arial", sans-serif;
    color: inherit;
  }

  .nav__item:not(:last-child) {
    margin-bottom: 20px;
  }

  .nav__menu-button {
    position: absolute;
    top: 22px;
    right: 8px;

    box-sizing: border-box;
    width: 30px;
    height: 30px;

    background: none;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;
  }

  .nav__menu-button--close {
    background-image: url("../img/icon-menu-close.svg");
  }

  .nav__menu-button--no-js {
    background: none;
  }
}

.promo__invite {
  box-sizing: border-box;
  margin-bottom: 54px;
}

.promo__place-n-date {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 26px;
  padding: 0;

  font-size: 12px;
  line-height: 124.4%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 5%;
}

.promo__slogan {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 44px;
  padding: 0;

  font-size: 24px;
  line-height: 124.4%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  color: #ffffff;
}

.promo__note {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;

  font-size: 14px;
  line-height: 124.4%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #ffffff;
}

.promo__join-button {
  min-width: 223px;

  background-color: #52d1bc;
  background-clip: padding-box;
  border: 14px solid rgba(82, 209, 188, 0.5);
}

.promo__reviews-list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  list-style: none;
}

.promo__review {
  box-sizing: border-box;
  width: 231px;
  height: 231px;
  margin: 0 auto;
  padding: 40px;
  padding-top: 45px;
  overflow: hidden;

  text-align: center;

  background-color: #ffffff;
  background-image: url("../img/promo-review-circle-bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}

.promo__review-content {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  text-align: center;
}

.promo__review-text {
  display: block;
  box-sizing: border-box;
  margin-bottom: 2px;

  font-size: 14px;
  line-height: 150%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #2f80ed;
}

.promo__review-rate {
  display: block;
  box-sizing: border-box;

  font-size: 34px;
  line-height: 150%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  color: #2f80ed;
}

.promo__review-author {
  display: block;
  box-sizing: border-box;
  margin-bottom: 4px;

  text-align: center;
}

.promo__review-name {
  display: block;
  box-sizing: border-box;
  margin-bottom: 2px;

  font-style: normal;
  font-size: 14px;
  line-height: 150%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #2f80ed;
}

.promo__review-company {
  display: block;
  box-sizing: border-box;

  font-style: normal;
  font-size: 14px;
  line-height: 150%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  color: #2f80ed;
}

.promo__review-link {
  display: block;
  box-sizing: border-box;
  width: 84px;
  margin: 0 auto;

  font-size: 10px;
  line-height: 150%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #2f80ed;
  text-decoration: none;

  background: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: center right;
}

.statistics {
  box-sizing: border-box;
  padding: 8px 8px 56px;
}

.statistics__heading {
  box-sizing: border-box;
  margin-bottom: 60px;

  font-size: 24px;
  line-height: 124.41%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  text-align: center;
  color: #000000;
}

.statistics__points {
  box-sizing: border-box;
  margin: 0;
  padding: 0 12px;

  list-style: none;
}

.statistics__point {
  box-sizing: border-box;
  margin-bottom: 10px;
  padding: 50px;

  font-size: 14px;
  line-height: 143.55%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #636363;

  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 30px 100px rgba(47, 55, 237, 0.1);
}

.statistics__rate {
  display: block;
  box-sizing: border-box;
  margin-bottom: 14px;

  font-size: 24px;
  line-height: 143.55%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  color: #2f80ed;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.who-needs {
  box-sizing: border-box;
  min-height: 986px;
  padding: 50px 70px;

  background-image: url("../img/bg-who-needs.svg");
  background-repeat: no-repeat;
  background-position: -107px 24px;
}

.who-needs__heading {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  font-size: 24px;
  line-height: 124.41%;
  font-family: "montserrat-bold", "Arial", sans-serif;
  color: #000000;
}

.who-needs__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.who-needs__item-heading {
  box-sizing: border-box;

  font-size: 14px;
  line-height: 143.55%;
  font-family: "montserrat-semibold", "Arial", sans-serif;
  color: #2f80ed;
  text-transform: uppercase;
}

.who-needs__item-description {
  box-sizing: border-box;

  font-size: 14px;
  line-height: 143.55%;
  font-family: "montserrat-regular", "Arial", sans-serif;
  color: #636363;
}
